HTML SCSSResult Skip Results Iframe EDIT ON body {
    width: 100vw;
    height: 100vh;
    display: flex;
    position: relative;
    background: linear-gradient(to bottom right, #6889FF, #C668FF);
    align-items: center;
    justify-content: center;
  }
  .btn-flip {
    opacity: 1;
    outline: 0;
    color: black;
    line-height: 40px;
    position: relative;
    text-align: center;
    letter-spacing: 1px;
    display: inline-block;
    text-decoration: none;
  }
  .btn-flip:hover:after {
    opacity: 1;
    transform: translateY(0) rotateX(0);
  }
  .btn-flip:hover:before {
    opacity: 0;
    transform: translateY(50%) rotateX(90deg);
  }
  .btn-flip:after {
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    color: black;
    display: block;
    transition: 0.5s;
    position: absolute;
    background: linear-gradient(to bottom right, #6889FF, #C668FF);
    content: attr(data-back);
    transform: translateY(-50%) rotateX(90deg);
  }
  .btn-flip:before {
    top: 0;
    left: 0;
    opacity: 1;
    color: black;
    display: block;
    padding: 0 30px;
    line-height: 40px;
    transition: 0.5s;
    position: relative;
    background: linear-gradient(to bottom right, #6889FF, #C668FF);
    content: attr(data-front);
    transform: translateY(0) rotateX(0);
  }
  